$BGC:red;

* {
    box-sizing: border-box;
}

body {
    overflow: hidden;
}

video {
    position:absolute;
    z-index:0;
    width:100%;
    height:100vh;
    object-fit: cover;
    margin:0;
    padding:0;
    top:0;
    left:0;
    border:none;
}

.App{
    position:absolute;
    width:100vw;
    height: 100vh;
    margin:0;
    padding:0;
    /*background-color: gray;*/
    overflow: hidden;
    z-index: 2;
    
    /*display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;*/
}

.blob {
    background-color: white;
    border-radius: 15px;
    padding: 10px;
    border: 2px black solid;
}

header {
    font-size: 69px;
    white-space: nowrap;
    position: absolute;
    top: 220px;
    left: 50%;
    transform: translateX(-50%);
}

.selectors {
    position: absolute;
    top: 37%;
    font-size: 35px;
    display: grid;
    grid-template-columns: auto auto;
}

#BreakSelector {
    left: 33%;
    transform: translateX(-50%);
    #break-label{
        grid-column: 1/ span 2;
    }
    #break-length{
        margin: 0 auto;
        grid-column: 1/ span 2;
    }
}

#SessionSelector {
    left: 66%;
    transform: translateX(-50%);
    #session-label{
        grid-column: 1/ span 2;
    }
    #session-length{
        margin: 0 auto;
        grid-column: 1/ span 2;
    }
}

#mainDisplay {
    position: absolute;
    top: 65%;
    left:50%;
    transform: translate(-50%, -50%);
    padding: 10px;
    width: 25%;
    height: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 55px;
    border: 2px solid black;
}

button:hover {
    animation: pulse 1s;
    box-shadow: 0 0 0 2em transparent;
}